html{font-size:62.5%}body{font-family:Helvetica, Arial, sans-serif;font-size:1.6rem;overflow:hidden;width:100%;height:100%}#canvas-viz{position:absolute;top:0;width:100%;height:100%;margin:0;z-index:-999}.toggle-svg{fill-opacity:0;stroke:white;stroke-width:.1vh;stroke-dasharray:18.22124vh;stroke-linecap:round;transform-origin:50% 50%}.toggle-button{width:6vh;height:6vh;border-radius:3vh;padding:0;margin:0 .75vh 0 0;color:inherit;border:none;overflow-wrap:break-word;background-color:rgba(0,0,0,0)}.toggle-button:focus{outline:none}.toggle-button:hover{cursor:pointer}.pending-toggle-button{background:rgba(249,243,168,0.5)}.active-toggle-button{background:rgba(149,219,177,0.5)}.stopped-toggle-button{background:rgba(255,255,255,0)}.slider{-webkit-appearance:none;width:65%;left:0;margin-bottom:2vh;height:0.25vh;background:#fff;outline:none;opacity:0.6;-webkit-transition:.2s;transition:opacity .2s}.slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:1.5vh;height:1.5vh;border-radius:50%;background:#fff;cursor:pointer}.slider::-moz-range-thumb{width:1.5vh;height:1.5vh;border-radius:50%;background:#fff;cursor:pointer}.slider-header{width:100%}.control-panel{opacity:0;transition-duration:200ms;padding:3vh;position:absolute;height:100vh;top:0px;left:0px;color:white;z-index:-1}.control-panel:hover{opacity:1;visibility:visible;background-color:rgba(0,0,0,0.3);transition-duration:200ms}.control-panel-section{position:absolute}.control-panel h2{font-size:2.5vh;margin:1vh 0 1vh 0}.control-panel h3{font-size:1.75vh;margin:1vh 0 1vh 0;font-weight:normal}.control-panel__section{margin:0 0 3vh 0}.control-panel__col{display:flex;flex-direction:column}.control-panel__row{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-content:space-around;align-items:center}.control-panel__hide-button{background:rgba(255,255,255,0.15);opacity:inherit;visibility:inherit;color:inherit;position:absolute;font-size:1vh;vertical-align:center;text-align:center;padding:0;top:0;right:0;width:3vh;height:3vh;margin:1.5vh;border-radius:1.5vh}.control-panel__hide-button:hover{cursor:pointer}.control-panel__hide-button:focus{outline:none}.control-panel__button{background:rgba(255,255,255,0.15);color:inherit;padding:0.5vh;font-size:1.5vh;width:12.75vh;margin:0 .75vh .75vh 0}.control-panel__button:focus{outline:none}.control-panel__button:hover{cursor:pointer}.control-panel__dropdown-button{opacity:1;visibility:hidden;background:rgba(255,255,255,0);width:9vh;height:6vh;border-radius:0 3vh 3vh 0;border:.1vh solid;border-left-style:none;margin-top:3vh;color:white;font-size:1.75vh;text-align:center;padding:0;position:absolute;left:-.3vh;top:0;z-index:-1}.control-panel__dropdown-button:hover{cursor:pointer}.control-panel__dropdown-button:focus{border:none}.hover-for-controls{color:white;display:inline-block;font-size:1.5vh;padding:0;margin:0;transition-duration:200ms}div.hover-for-controls{padding:1vh}.hover-for-controls #arrow{font-size:1.5vh;transition-duration:200ms;transform-origin:50% 50%;transform:rotate(0deg)}@media (max-width: 48rem){.hover-for-controls{visibility:hidden}.control-panel{visibility:hidden;transition-duration:0ms;opacity:1;background-color:rgba(0,0,0,0.3)}.control-panel:hover{transition-duration:0ms}.control-panel__dropdown-button{visibility:visible}}.song-landing-page{color:#fff;background:#1f262e;transition-duration:1500;width:100vw;height:100vh;padding-top:15vh;display:flex;flex-direction:column;align-content:center;justify-content:top;align-items:center;z-index:10}.song-landing-page svg{width:30vw}.song-landing-page p{font-size:1.75vh;margin:0.5vh}.song-landing-page button{font-size:2vh;width:12vh;height:6vh;border:.1vh white solid;padding:0;margin-top:3vh;color:inherit;overflow-wrap:break-word;background-color:rgba(255,255,255,0)}.song-landing-page button:hover{background-color:rgba(255,255,255,0.2);cursor:pointer}.song-landing-page button:focus{outline:none}
